.project-card{
    display: flex;
    flex:1;
    justify-content: space-between;
    align-items: center;
    margin: 16px 0px;
    padding: 8px 0px;
}

.project-image{
    width:40%;
}

.project-info{
    margin-right: 24px;
    text-align: justify;
}

.project-title{
    font-size: 20px;
    font-weight: 600;
}

.project-about{
    font-size: 18px;
}

.project-tags{
    display: flex;
    max-width: 100%;
    flex-wrap: wrap;
}

.project-tag{
    padding: 4px 8px;
    background-color: aquamarine;
    border-radius: 4px;
    margin-right: 6px;
    margin-bottom: 4px;
}

.project-links a{
    text-decoration: none;
    color: black;
}

.project-links{
    margin-top: 6px;
    display: flex;
}

.link-button{
    display: flex;
    justify-content: center;
    align-items: center;
    background-color: aquamarine;
    width: max-content;
    padding: 4px 8px;
    border-radius: 4px;
    margin: 6px;
}

.link-button i{
    display: flex;
    align-items: center;
    margin-right: 4px;
}

@media only screen and (max-width: 720px){
    .project-image{
        width: 70%;
    }

    .project-card{
        flex-direction: column-reverse;
    }
    .project-info{
        margin-right: 0px;
        margin-top: 12px;
    }
    .project-tags{
        max-width: 100%;
    }
}